<template>
  <div class="add-brand-page">
    <div class="crumbs-box ">
      <span class="crumbs">新增品牌</span>
    </div>
    <div class="content-page">
      <div class="flex-row item-row">
        <div class="left-title-box">
          <span class="xin">*</span>
          <span class="tit-name">品牌名称</span>
        </div>
        <div class="right-con-box">
          <input type="text" class="admin-input" v-model="form.brand_name" placeholder="请输入品牌名称" autocomplete="false">
        </div>
      </div>
      <div class="flex-row item-row">
        <div class="left-title-box">
          <span class="xin">*</span>
          <span class="tit-name">品牌分类</span>
        </div>
        <div class="right-con-box">
          <el-select v-model="form.cate_id" class="admin-select" placeholder="请选择品牌经营分类">
            <el-option
              v-for="(item, index) in brandClassifyList"
              :key="index"
              :label="item.cate_name"
              :value="item.id">
            </el-option>
          </el-select>
        </div>
      </div>
      <div class="flex-start item-row">
        <div class="left-title-box">
          <span class="xin">*</span>
          <span class="tit-name">品牌LOGO</span>
        </div>
        <div class="right-con-box">
          <ul class="img-ul">
            <li v-for="(item, index) in brandLogo" :key="index">
              <img :src="host+item.img_url" class="img">
              <img src="../../assets/ic_guanbi2.png" class="remove-img hover" @click="removeBrandLogo(index)">
            </li>
            <div class="upload-file-box" v-show="brandLogo.length<1">
              <img src="../../assets/ic_tianjia.png" class="add-img-icon hover">
              <el-upload
                action=""
                class="el-uploader"
                :auto-upload="false"
                :show-file-list="false"
                :multiple="true"
                :on-change="handleBrandLogoSuccess">
              </el-upload>
            </div>
          </ul>
          <div class="img-tips">
            * 点击上传品牌Logo，logo大小为<span class="c6">10px*100px</span>图片，大小不超过<span class="c6">300KB</span>；支持格式<span class="c6">png、jpeg、gif</span>格式，最多可上传<span class="c6">1</span>张
          </div>
        </div>
      </div>
      <div class="flex-start item-row">
        <div class="left-title-box">
          <span class="xin">*</span>
          <span class="tit-name">请上传商标证书</span>
        </div>
        <div class="right-con-box">
          <ul class="img-ul">
            <li v-for="(item, index) in certsList" :key="index">
              <img :src="host+item.img_url" class="img">
              <img src="../../assets/ic_guanbi2.png" class="remove-img hover" @click="removeCerts(index)">
            </li>
            <div class="upload-file-box" v-show="certsList.length<5">
              <img src="../../assets/ic_tianjia.png" class="add-img-icon hover">
              <el-upload
                action=""
                class="el-uploader"
                :auto-upload="false"
                :show-file-list="false"
                :multiple="true"
                :on-change="handleCertsSuccess">
              </el-upload>
            </div>
          </ul>
          <div class="img-tips">
            * 支持上传Word、PDF和图片类型的文件，图片类型支持<span class="c6">png、jpeg、gif</span>格式；数量限制为<span class="c6">5</span>个。
          </div>
        </div>
      </div>
      <div class="flex-row item-row">
        <div class="left-title-box">
          <span class="xin">*</span>
          <span class="tit-name">是否是品牌商</span>
        </div>
        <div class="flex-row">
          <div :class="['zi-ti hover', form.is_brand==1?'active':'']" @click="selectBrands(1)">
            <img src="../../assets/ic_xuanzhong.png" class="radio-icon" v-if="form.is_brand==1">
            <img src="../../assets/ic_weixuanzhong.png" class="radio-icon" v-else>
            <span>是</span>
          </div>
          <div :class="['zi-ti hover', form.is_brand==2?'active':'']" @click="selectBrands(2)">
            <img src="../../assets/ic_xuanzhong.png" class="radio-icon" v-if="form.is_brand==2">
            <img src="../../assets/ic_weixuanzhong.png" class="radio-icon" v-else>
            <span>否</span>
          </div>
        </div>
      </div>
      <!-- 是否是品牌商? 否 -->
      <div class="flex-col" v-show="form.is_brand==2">
        <div class="flex-start item-row">
          <div class="left-title-box">
            <span class="xin">*</span>
            <span class="tit-name">请上传品牌授权书</span>
          </div>
          <div class="right-con-box">
            <ul class="img-ul">
              <li v-for="(item, index) in brandImgs" :key="index">
                <img :src="host+item.img_url" class="img">
                <img src="../../assets/ic_guanbi2.png" class="remove-img hover" @click="removeBrandImgs(index)">
              </li>
              <div class="upload-file-box" v-show="brandImgs.length<5">
                <img src="../../assets/ic_tianjia.png" class="add-img-icon hover">
                <el-upload
                  action=""
                  class="el-uploader"
                  :auto-upload="false"
                  :show-file-list="false"
                  :multiple="true"
                  :on-change="handleBrandImgsSuccess">
                </el-upload>
              </div>
            </ul>
            <div class="img-tips">
              * 支持上传Word、PDF和图片类型的文件，图片类型支持格式<span class="c6">JPG/PNG/JPEG</span>格式；数量限制为<span class="c6">5</span>个。
            </div>
            <div class="edit-tips">编辑品牌商-N级代理商，点击加号增加下一级链路；若要删除请点击右上角删除小标。</div>
            <div class="agency-box">
              <ul class="agency-ul" id="agencyUl" v-show="agencyList.length>0">
                <li class="agency-item" v-for="(item, index) in agencyList">
                  <input type="text" class="admin-input agency-ipt" v-model="item.value" placeholder="请输入品牌商名称">
                  <img src="../../assets/ic_tianjia.png" class="add-agency-icon hover" @click="addAgencyName">
                  <img src="../../assets/ic_guanbi2.png" class="remove-agency-icon hover" @click="removeAgency(index)">
                </li>
              </ul>
              <div class="add-agency-text hover" @click="addAgencyName">添加</div>
            </div>
          </div>
        </div>
      </div>
      <div class="flex-start item-row">
        <div class="left-title-box">
          <span class="xin">*</span>
          <span class="tit-name">有效时间</span>
        </div>
        <div class="right-con-box">
          <div class="flex-row" style="margin-bottom: 15px;">
            <el-date-picker
              v-model="times"
              type="daterange"
              align="left"
              unlink-panels
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期">
            </el-date-picker>
            <div class="flex-center hover" style="margin-left: 20px;" @click="selectLongTime">
              <img src="../../assets/ic_weixuanzhong.png" style="margin-right: 5px;" v-if="!isLongTime">
              <img src="../../assets/ic_xuanzhong.png" style="margin-right: 5px;" v-else>
              <span>长期</span>
            </div>
          </div>
          <div class="img-tips">
            * 有效时间以授权书上的约定为准，若无明确时间请选择‘长期’。
          </div>
        </div>
      </div>
      <div class="flex-start item-row">
        <div class="left-title-box">
          <span class="xin">*</span>
          <span class="tit-name">品牌理念</span>
        </div>
        <div class="flex-row">
          <div class="text-area-box">
            <textarea name="" class="admin-textarea store-desc" v-model="brandDesc" @input="brandDescTextarea" placeholder="请填写品牌理念，支持500个字符的输入。"></textarea>
            <div class="desc-length">{{brandDescLength}}/500</div>
          </div>
        </div>
      </div>
      <div class="flex-row item-row">
        <div class="left-title-box">
          <span class="xin">*</span>
          <span class="tit-name">是否生效</span>
        </div>
        <div class="flex-row">
          <div :class="['zi-ti hover', form.status==1?'active':'']" @click="selectIsShengXiao(1)">
            <img src="../../assets/ic_xuanzhong.png" class="radio-icon" v-if="form.status==1">
            <img src="../../assets/ic_weixuanzhong.png" class="radio-icon" v-else>
            <span>是</span>
          </div>
          <div :class="['zi-ti hover', form.status==2?'active':'']" @click="selectIsShengXiao(2)">
            <img src="../../assets/ic_xuanzhong.png" class="radio-icon" v-if="form.status==2">
            <img src="../../assets/ic_weixuanzhong.png" class="radio-icon" v-else>
            <span>否</span>
          </div>
        </div>
      </div>
      <div class="admin-btn-box">
        <div class="btn btn1 hover" @click="gotoBack">返回</div>
        <div class="btn btn2 hover" @click="onSubmitData">提交</div>
      </div>
    </div>
  </div>
</template>
<script>
  import { GetBrandCateList, UploadImg, Brandstoreadd } from '@/axios/api'
  import { mapState } from 'vuex'
  export default {
    name: 'add-brand-page',
    data() {
      return {
        token: sessionStorage.getItem('token'),
        brandClassifyList: [],
        //
        times: '',
        isLongTime: false,
        isLongTimeTwo: false,
        brandDesc: '',
        brandDescLength: 0,
        agencyList: [
          {value: ''}
        ],
        brandLogo: [], // 品牌logo 取它下面的img_url
        certsList: [], // 商标证书
        brandImgs: [], // 品牌授权书
        form: {
          brand_name: '',
          cate_id: '',
          logo: '',
          is_brand: 1,
          certs: '',
          brand_img: '',
          brand_time_start: '',
          brand_time_end: '',
          notion: '',
          status: 1
        }

      }
    },
    computed: {
      ...mapState(["host"])
    },
    components: {},
    mounted() {
      setTimeout(function(){
        $(".el-input__inner").css({
          width: '400px',
          height: '50px'
        });
        $(".el-upload").css({
          width: '100%',
          height: '100%'
        })
      }, 50);

      this.getBrandCateList()
    },
    methods: {
      // 获取品牌分类
      getBrandCateList() {
        GetBrandCateList({
          token: this.token
        }).then(res => {
          if (res.status == 1) {
            this.brandClassifyList = res.data;
          } else {
            this.$message.error(res.msg)
          }
        })
      },
      gotoBack() {
        this.$router.back(-1)
      },
      // 添加品牌logo
      handleBrandLogoSuccess(file) {
        const isLt2M = file.size / 1024 / 1024 < 300/1024;
        if (['image/jpeg', 'image/jpg', 'image/png'].indexOf(file.raw.type) <= -1) {
          this.$message.error('请上传正确的图片格式');
          return false
        }
        if (!isLt2M) {
          this.$message.error('上传图片大小不能超过300KB哦!');
          return false
        }
        let param = new FormData();
        param.append('token', this.token);
        param.append('file', file.raw);
        UploadImg(param).then(res => {
          // console.log(res)
          if (res.status === 1) {
            this.brandLogo.push({
              img_url: res.data.src
            })
          } else {
            this.$message.warning(res.msg)
          }
        })
      },
      removeBrandLogo(index) {
        this.brandLogo.splice(index, 1)
      },
      // 添加商标证书
      handleCertsSuccess(file) {
        const isLt2M = file.size / 1024 / 1024 < 200;
        if (['image/jpeg', 'image/jpg', 'image/png'].indexOf(file.raw.type) <= -1) {
          this.$message.error('请上传正确的图片格式');
          return false
        }
        if (!isLt2M) {
          this.$message.error('上传图片大小不能超过2MB哦!');
          return false
        }
        let param = new FormData();
        param.append('token', this.token);
        param.append('file', file.raw);
        UploadImg(param).then(res => {
          // console.log(res)
          if (res.status === 1) {
            this.certsList.push({
              img_url: res.data.src
            })
          } else {
            this.$message.warning(res.msg)
          }
        })
      },
      removeCerts(index) {
        this.certsList.splice(index, 1)
      },
      // 品牌授权书
      handleBrandImgsSuccess(file) {
        const isLt2M = file.size / 1024 / 1024 < 200;
        if (['image/jpeg', 'image/jpg', 'image/png'].indexOf(file.raw.type) <= -1) {
          this.$message.error('请上传正确的图片格式');
          return false
        }
        if (!isLt2M) {
          this.$message.error('上传图片大小不能超过2MB哦!');
          return false
        }
        let param = new FormData();
        param.append('token', this.token);
        param.append('file', file.raw);
        UploadImg(param).then(res => {
          // console.log(res)
          if (res.status === 1) {
            this.brandImgs.push({
              img_url: res.data.src
            })
          } else {
            this.$message.warning(res.msg)
          }
        })
      },
      removeBrandImgs(index) {
        this.brandImgs.splice(index, 1)
      },
      // 是否是品牌商
      selectBrands(num) {
        this.form.is_brand = num
      },
      // 是否长期 (是品牌商)
      selectLongTime() {
        this.isLongTime = !this.isLongTime
      },
      // 是否生效 (是品牌商)
      selectIsShengXiao(num) {
        this.form.status = num
      },
      // 品牌理念 (是品牌商)
      brandDescTextarea() {
        let leng =  this.brandDesc.length;
        if (leng > 500) {
          leng = 500;
          this.brandDesc = (this.brandDesc).substr(0, 500)
        }
        this.brandDescLength = leng
      },
      // 添加代理商-品牌商
      addAgencyName() {
        this.agencyList.push({value: ''})
      },
      removeAgency(index) {
        this.agencyList.splice(index, 1)
        // if (this.agencyList.length > 1) {
        //
        // } else {
        //   this.$message.info('至少保留一个代理商')
        // }

      },
      onSubmitData() {
        let formData = this.form;
        if (!formData.brand_name) {
          this.$message.error('请输入品牌名称');
          return
        }
        if (!formData.cate_id) {
          this.$message.error('请选择品牌分类');
          return
        }
        let brandLogo = this.brandLogo;
        if(brandLogo.length <= 0) {
          this.$message.error('请上传品牌LOGO');
          return
        } else {
          let arr = [];
          for(let i in brandLogo) {
            arr.push(brandLogo[i].img_url)
          }
          formData.logo = arr.join(",")
        }
        let certsList = this.certsList;
        if(certsList.length <= 0) {
          this.$message.error('请上传商标证书');
          return
        } else {
          let arr = [];
          for(let i in certsList) {
            arr.push(certsList[i].img_url)
          }
          formData.certs = arr.join(",")
        }
        if (formData.is_brand == 2) {
          // 不是品牌商
          let brandImgs = this.brandImgs;
          if(brandImgs.length <= 0) {
            this.$message.error('请上传品牌授权书');
            return
          } else {
            let arr = [];
            for(let i in brandImgs) {
              arr.push(brandImgs[i].img_url)
            }
            formData.brand_img = arr.join(",")
          }
          let agencyList = this.agencyList;
          let brand_url = [];
          if(agencyList.length > 0) {
            for(let i in agencyList) {
              if(agencyList[i].value) {
                brand_url.push(agencyList[i].value)
              }
            }
          }
          formData.brand_url = brand_url.join(',');
        }
        let times = this.times;
        if (this.isLongTime) {
          // 选择了长期
          formData.brand_time_start = 1;
          formData.brand_time_end = 1;
        } else {
          if(!times) {
            this.$message.error('请选择有效时间');
            return
          } else {
            formData.brand_time_start = this.createTime(times[0]);
            formData.brand_time_end = this.createTime(times[1]);
          }
        }
        if(!this.brandDesc) {
          this.$message.error('请输入品牌理念');
          return
        } else {
          formData.notion = this.brandDesc
        }
        formData.token = this.token;
        Brandstoreadd(formData).then(res => {
          if(res.status == 1) {
            this.$message.success('添加品牌成功，待审核');
            // let arr = this.$store.state.addBrandInfo;
            // arr.push({
            //   id: res.data.brand_id,
            //   brand_name: res.data.brand_name,
            //   logo: res.data.logo,
            //   active: true
            // });
            // this.$store.state.addBrandInfo = arr;
            this.gotoBack();
          } else {
            this.$message.error(res.msg)
          }
        });
      },
      addTime(m){return m<10?'0'+m:m },
      createTime(value){
        let time = new Date(value);
        let year = time.getFullYear();
        let month = time.getMonth()+1;
        let date = time.getDate();
        let hours = time.getHours();
        let minutes = time.getMinutes();
        let seconds = time.getSeconds();
        // return year+'-'+this.addTime(month)+'-'+this.addTime(date)+' '+this.addTime(hours)+':'+this.addTime(minutes)+':'+this.addTime(seconds);
        return year+'-'+this.addTime(month)+'-'+this.addTime(date);
      },//2017-05-08 10:31:27
    }
  }
</script>
<style lang="less" scoped>
  .el-select-dropdown__item {
    font-size: 14px;
    padding: 0 20px;
    position: relative;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    color: #606266;
    height: 34px;
    line-height: 34px;
    box-sizing: border-box;
    cursor: pointer;
  }
  .el-select-dropdown__item.selected{
    color: #409EFF !important;
  }
  .el-input__inner{
    width: 400px;
    height: 50px;
  }
</style>



